:root {
    --input-background: #f0f0f0;
    --input-color: #5d7488;
    --white: #ffffff;
    --black: #000000;
    --error: #ff5659;
    --border-color: #d9d9d9;
    --grey: #747474;
    --text-blue: #7695b0;
    --text-grey: #303030;
    --button-grey: #f3f0f1;
    --border: #020202;
    --button-text: #6f6cde;
    --ligth-purple: #E2E1F8;
    --purple: #6f6cde;
    --green: #79e3b6;
  }
  
.App {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 700px;
  margin: 0 auto;
}

.container{
  max-width: 300px;
  margin: 0 auto;
  width: 100%;
}

html, body, #root {
    width: 100%;
    /* margin: 0 auto; */
    height: auto;
    min-height: 100vh;
    background: linear-gradient(0deg, rgba(221,221,221,1) 0%, rgba(129,160,198,1) 65%, rgba(141,129,198,1) 100%);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
}

button, textarea, ul {
  outline: none;
}

#root {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    overflow-x: hidden;
    position: relative;

}

.wrapper {
  width: 100%;
  height: auto;
  display: flex;
  min-height: calc(100% - 141px);
  justify-content: center;
  display: grid;
  grid-template-columns: 30% 30% 30%;
  grid-column-gap: 2.333vw;
  margin-top: 10vh;
}

.buttonFixed {
  position: fixed;
  right: 0;
  top: 3vh;
  box-shadow: -6px -6px 10px rgba(0, 0, 0, 0.2), 6px 6px 10px rgba(0, 0, 0, 0.2);
}

.main-title{
  margin: 4vh auto 0;
  color: var(--text-grey);
  font-family: 'Alegreya Sans', sans-serif;
  font-size: calc(35px + 0.313vw);
  opacity: .2;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 60%;
  }
  .main-title{
    margin: 10vh auto 0;
  }
}  
